<template>
  <div class="container">
    <button class="btn-export" @click="exportToGoogleSheets($event)">
      Export to Goole Sheets
    </button>
    <div class="tab-menu">
      <div class="tab-menu-element active" @click="select(0, $event)">
        количество гостей по городам
      </div>
      <div class="tab-menu-element" @click="select(1, $event)">
        средняя сумма визита гостей по городам
      </div>
      <div class="tab-menu-element" @click="select(2, $event)">
        сумма визитов по городу
      </div>
      <div class="tab-menu-element" @click="select(3, $event)">
        сумма визитов по гостям
      </div>
    </div>
    <table>
      <thead>
        <tr>
          <th>#</th>
          <th @click="sortBy($event.target)">{{ arg1[active] }}</th>
          <th @click="sortBy($event.target)">{{ arg2[active] }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(data, index) in data" v-bind:key="data">
          <td>{{ index }}</td>
          <td>{{ data.city || data.guest_id }}</td>
          <td>
            {{
              data.number_of_guests ||
                data.average_visit_summary ||
                data.visit_summary
            }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style lang="scss" scoped>
