  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.btn-export {
  max-width: 300px;
  padding: 10px 20px;
  align-self: flex-end;
  margin: 20px 20px 0;
  color: white;
  font-weight: 700;
  background: #31ac70;
  border: none;
  outline: none;
  border-radius: 0.3rem;
  cursor: pointer;

  &:hover {
    background: #339b68;
  }
}

.tab-menu {
  margin: 20px 20px 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;

  font-weight: 700;
  text-align: center;

  &-element {
    padding: 10px;
    cursor: pointer;
    margin-top: 5px;
    margin-right: 5px;
    background: white;
    border-radius: 0.5rem;
    border: 1px solid #31ac70;

    transition: all 300ms;

    &:hover {
      background: #1d6e47 !important;
      border: 1px solid #1d6e47;
      color: white;
    }
  }

  .active {
    background: #31ac70;
    color: white;
  }
}

table {
  text-align: left;
  border-collapse: collapse;
  margin: 10px 20px;
  border: 1px solid #31ac70;
  cursor: pointer;

  thead {
    padding: 10px;

    & th:nth-child(1) {
      text-align: center;
      width: 20px;
    }

    & th:nth-child(2) {
      width: 50%;
    }

    & th:nth-child(3) {
      width: 50%;
    }

    tr {
      th {
        padding: 10px;

        &:hover {
          background: rgb(235, 235, 235);
        }
      }

      th.sorted[data-order="1"],
      th.sorted[data-order="-1"] {
        position: relative;
      }

      th.sorted[data-order="1"]::after,
      th.sorted[data-order="-1"]::after {
        right: 8px;
        position: absolute;
      }

      th.sorted[data-order="-1"]::after {
        content: "▼";
      }

      th.sorted[data-order="1"]::after {
        content: "▲";
      }
    }
  }

  tbody {
    padding: 10px;

    & td:nth-child(1) {
      text-align: center;
    }

    tr {
      td {
        padding: 10px;
      }

      &:nth-child(odd) {
        color: white;
        background: #31ac70;
      }
