</style>

<script>
import axios from "axios";
// let url = "http://localhost:3000/";
let url = "https://dashboard34.herokuapp.com/";
// const { google } = require("googleapis");

export default {
  name: "Home",
  data() {
    return {
      arg0: [
        "number_of_guests_per_city",
        "average_visit_summary_per_city",
        "visit_summary_per_city",
        "visit_summary_per_guest",
      ],
      arg1: ["город", "город", "город", "гость"],
      arg2: ["кол-во гостей", "средння сумма", "сумма", "сумма"],
      data: [],
      active: 0,
    };
  },
  methods: {
    select: function(selected, event) {
      this.active = selected;
      document.getElementsByClassName("active")[0].classList.toggle("active");
      event.target.classList.toggle("active");
      document.getElementsByClassName("sorted")[0].classList.toggle("sorted");
      this.getData();
    },
    getData: function() {
      axios.get(url + "visits/" + this.arg0[this.active]).then((data) => {
        this.data = data.data.data;
      });
    },
    exportToGoogleSheets: function(event) {
      event.target.innerHTML = "Loading...";
      event.target.disabled = true;
      axios
        .get(url + "export/visits/" + this.arg0[this.active])
        .then((data) => {
          window.open(data.data.url);
          event.target.disabled = false;
          event.target.innerHTML = "Export to Goole Sheets";
        });
    },

    sortBy: function(target) {
      const order = (target.dataset.order = -(target.dataset.order || -1));
      const index = [...target.parentNode.cells].indexOf(target);
      const collator = new Intl.Collator(["en", "ru"], { numeric: true });
      const comparator = (index, order) => (a, b) =>
        order *
        collator.compare(
          a.children[index].innerHTML,
          b.children[index].innerHTML
        );

      for (const tBody of target.closest("table").tBodies)
        tBody.append(...[...tBody.rows].sort(comparator(index, order)));

      for (const cell of target.parentNode.cells)
        cell.classList.toggle("sorted", cell === target);
    },
  },
  created: function() {
    this.getData();
    // this.exportToGoogleSheets();
  },
};
</script>
